import React from 'react';
import classNames from 'classnames/bind';
import { ModalProps, ModalSize } from 'core/types/modals';
import ModalOverlay from 'shared/ModalOverlay';

import { ScrollArea } from './ScrollArea';
import Footer from '../Footer';
import Header from '../Header';

import styles from './styles.scss';

const cn = classNames.bind(styles);

type DefaultProps = {
  size: ModalSize;
  zIndex: number;
  selector: string;
  dataTestId: string;
  isFullscreen: boolean;
  withoutScroll: boolean;
};

const DEFAULT_PROPS: DefaultProps = {
  zIndex: 400,
  size: 'default',
  selector: 'body',
  isFullscreen: false,
  dataTestId: 'ds-modal',
  withoutScroll: false
};

export const Modal: React.FC<ModalProps> = ({
  label,
  onClose,
  children,
  onOverlayClick,
  footerItemsLeft,
  footerItemsRight,
  size = DEFAULT_PROPS.size,
  zIndex = DEFAULT_PROPS.zIndex,
  selector = DEFAULT_PROPS.selector,
  dataTestId = DEFAULT_PROPS.dataTestId,
  isFullscreen = DEFAULT_PROPS.isFullscreen,
  withoutScroll = DEFAULT_PROPS.withoutScroll
}) => {
  if (!children) {
    return null;
  }

  const wrapperClassName = cn(
    'wrapper',
    `wrapper--${ size }`,
    { 'wrapper--fullscreen': isFullscreen }
  );

  return (
    <ModalOverlay
      selector={ selector }
      onClick={ onOverlayClick }
      zIndex={ zIndex }
    >
      <div
        className={ wrapperClassName }
        data-test-id={ dataTestId }
      >
        <Header
          label={ label }
          size={ size }
          onClose={ onClose }
        />
        <ScrollArea isFullscreen={ isFullscreen } withoutScroll={ withoutScroll }>
          { children }
        </ScrollArea>
        <Footer
          size={ size }
          footerItemsLeft={ footerItemsLeft }
          footerItemsRight={ footerItemsRight }
        />
      </div>
    </ModalOverlay>
  );
};

export const StoryComponent: React.FC<ModalProps> = () => null;
StoryComponent.defaultProps = DEFAULT_PROPS;
